.title
  margin: 0

.text
  margin: 0

.menu
  display: flex
  align-items: center
  margin: 0
  padding: 0
  list-style: none

  &__item
    margin-right: 40px
    &:last-child
      margin-right: 0
    &:hover .menu__link
      &::after
        width: 100%
        left: 0

  &__link
    position: relative
    color: $white
    &::after
      content: ""
      position: absolute
      width: 0
      border-top: 2px dashed $yellow-orange
      right: 0
      top: 120%
      transition: all .4s

.logo
  margin-bottom: 40px
  max-width: 170px
  line-height: 0

  &__img
    width: 100%
    height: auto

.link
  padding: 20px 45px
  background-color: $yellow-orange
  color: $white
  border-radius: 0px 20px
  transition: border-radius .4s
  &:hover
    border-radius: 20px 0
